body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

h3 strong {
    color: #A80C1E;
    display: block;
    font-size: 55px;
    font-weight: normal;
    letter-spacing: -5px;
    line-height: 1em;
    padding-bottom:5mm;
}

.hobbies {
    color: #A80C1E;
    display: block;
    font-size: 30px;
    font-weight: bold;
    line-height: 1em;
    padding-bottom: 10mm;
}

nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

    nav a:hover {
        background: mediumturquoise;
        color: white;
    }

#page-body {
  max-width: 850px;
  margin: auto;
  padding-top: 64px;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

* {
  box-sizing: border-box;
}

.column {
  float: left;
  padding: 10px;
}

.left {
  width: 75%;
}

.right {
    width: 25%;
    display: grid;
}
.fab{
    padding:2mm;
}
/* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
}
.carousel-inner > .item > img,
.carousel-inner > .item > a > img {
    width: 70%;
    margin: auto;
}


#add-comment-div {
    display:grid;
    border-bottom: 2px solid black;
    padding: 16px;
}

    #add-comment-div h3 {
        margin-bottom: 16px;
    }

    #add-comment-div input, textarea {
        display: block;
        width: 300px;
        padding: 8px;
        border-radius: 8px;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-size: 18px;
        border: 2px solid black;
        margin-bottom: 16px;
    }

    #add-comment-div button {
        width: 220px;
        background-color: mediumturquoise;
        icon: auto;
    }

#upvotes-div button, p {
    display: inline-block;
    margin-right: 16px;
}
#upvotes-div button {
    border-color: mediumturquoise;
    background-color: mediumturquoise;
}

.comment {
    border-bottom: 2px solid black;
    padding: 16px;
}

